<template>
  <div>
    <div class="container max-width-adaptive-md margin-bottom-lg">
      <events-list v-if="!isLoading" :events="computedEvents">
        <li
          slot-scope="event"
          data-theme="default"
          class="stack-cards__item bg radius-lg shadow-md js-stack-cards__item"
        >
          <div class="grid">
            <div class="col-6 flex items-center height-100%">
              <div class="text-component padding-md">
                <h2>{{ event.name }}</h2>
                <p class="display@xs">Address: {{ event.address }}</p>
                <p class="display@xs">Event Prices:</p>
                <div v-for="check in event.event_kinds" :key="check.id">
                  <ul>
                    <li>{{ check.name }} for ₦ {{ parseFloat(check.price).toFixed(2) }}</li>
                  </ul>
                </div>
                <br />
                <p>
                  <router-link
                    :to="{ name: 'event', params: { code: event.code } }"
                    class="btn btn--accent"
                  >Attend Event</router-link>
                </p>
              </div>
            </div>
            <div class="col-6 height-100%">
              <img
                class="block width-100% height-100% object-cover"
                :src="event.image"
                @error="myDefaultImage"
              />
            </div>
          </div>
        </li>
      </events-list>
    </div>
    <div class="container max-width-adaptive-sm">
      <div class="text-component line-height-lg v-space-md">
        <p></p>
      </div>
    </div>
  </div>
</template>
<style scoped>
.object-cover {
  background-color: #b0897e;
}
</style>
<script>
import EventsList from "./EventsList";
import { repositoryFactory } from "../respositories/repositoryFactory";
const EventRepository = repositoryFactory.get("events");

export default {
  name: "Home",
  components: {
    EventsList
  },
  data() {
    return {
      isLoading: false,
      events: []
    };
  },
  created() {
    this.getEvents();
  },
  methods: {
    getEvents: async function() {
      this.isLoading = true;
      const { data } = await EventRepository.get();
      this.isLoading = false;
      this.events = data.data;
    },
    myDefaultImage(e) {
      e.target.src = "assets/img/img-1.jpg";
    }
  },
  computed: {
    computedEvents() {
      return this.events.slice(0, 10);
    }
  }
};
</script>
