<template>
  <div>
    <div class="container max-width-adaptive-md margin-bottom-lg" v-if="event_details.active">
      <center>
        <p v-if="isLoading">
          <center>
            <small>Loading......</small>
          </center>
        </p>
        <p v-else>
          <small>
            <router-link :to="{ name: 'event', params: { code: event_details.code } }">Go back</router-link>
          </small>
        </p>
      </center>
      <b-alert v-if="success" variant="success" show>Payment was completed successfully</b-alert>
      <b-alert v-if="failed" variant="warning" show>{{this.error? this.error : "Something went wrong with this payment, please try again or send an email to hello@tonidev.net"}}</b-alert>
      <b-form @submit="onSubmit" @reset="onReset" v-if="show && !startPayment" @submit.stop.prevent>
        <b-form-group
          id="input-group-1"
          label="Email address:"
          label-for="input-1"
          description="We'll never share your email with anyone else."
        >
          <b-form-input
            id="input-1"
            v-model="form.email"
            type="email"
            required
            placeholder="Enter email"
          ></b-form-input>
        </b-form-group>

        <b-form-group id="input-group-2" label="Your Name:" label-for="input-2">
          <b-form-input
            id="input-2"
            v-model="form.name"
            :state="validation"
            required
            placeholder="Enter name"
          ></b-form-input>
          <b-form-invalid-feedback :state="validation">Your name must be 4-30 characters long.</b-form-invalid-feedback>
          <b-form-valid-feedback :state="validation">You've got a cute name :(</b-form-valid-feedback>
        </b-form-group>

        <h4>Details of Payment</h4>
        <p>
          Title of Event : {{event_details.event_name}}
          <br />
          Amount: NGN {{event_details.price}}.00
          <br />
          Date: {{event_details.date}}
        </p>
        <b-button
          :disabled="clicked"
          type="submit"
          variant="primary"
        >{{this.clicked ? 'Processsing ..... ': 'Buy Ticket..'}}</b-button>
        <b-button type="reset" variant="danger">Reset</b-button>
      </b-form>
      <center>
        <div v-if="startPayment">
          <h4>Details of Payment</h4>
          <p>
            Title of Event : {{event_details.event_name}}
            <br />
            Amount: NGN {{event_details.price}}.00
            <br />
            Date: {{event_details.date}}
          </p>
           <div v-if="paymentComplete">
                <a class="downloadButton" :href="buttonUrl" target="blank"> Download Receipt/Ticket </a>
            </div>
            <div v-else>
                <Rave
                    :email="payment.email"
                    :amount="payment.amount"
                    :reference="payment.reference"
                    :rave-key="raveKey"
                    :callback="callback"
                    :close="close"
                    :metadata="meta"
                    :customerFirstname="form.name"
                    paymentOptions="card,barter,account,ussd"
                    hostedPayemt="1"
                    customTitle="Golang Test"
                    currency="NGN"
                    country="NG"
                />
            </div>
        </div>
      </center>
    </div>
    <small v-else>
      <center>
        <router-link to="/">Go home</router-link>
        <br />This event does not exist or this ticket kind is sold out
        <br />
      </center>
    </small>
  </div>
</template>
<script>
import { repositoryFactory } from "../respositories/repositoryFactory";
import { RAVE_publicKey } from "../const/http";
import Rave from "vue-ravepayment";
const EventRepository = repositoryFactory.get("events");
const RAVE_key = RAVE_publicKey
export default {
  name: "checkout",
  components: {
    Rave
  },
  data() {
    return {
      raveKey: RAVE_key,
      isLoading: false,
      failed: false,
      buttonUrl: "",
      error:"",
      success: false,
      paymentComplete: false, 
      event_details: [],
      payment: [],
      meta:[{
          ticket : parseInt(localStorage.getItem("event_id"))
      }],
      form: {
        email: "",
        name: "",
        event_id: parseInt(localStorage.getItem("event_id"))
      },
      button: {
        text: "Buy Ticket"
      },
      clicked: false,
      show: true,
      startPayment: false
    };
  },
  created() {
    this.getTicket();
    // const script = document.createElement("script");
    // script.src = 'https://ravesandboxapi.flutterwave.com/flwv3-pug/getpaidx/api/flwpbf-inline.js';
    // document.getElementsByTagName("head")[0].appendChild(script);
  },
  methods: {
    getTicket: async function() {
      this.isLoading = true;
      var event_id = localStorage.getItem("event_id");
      if (event_id && parseInt(event_id, 10)) {
        const { data } = await EventRepository.getTicket(event_id);
        this.isLoading = false;
        this.event_details = data.data;
      }
    },
    onSubmit: async function(evt) {
      evt.preventDefault();
      this.clicked = !this.clicked;
      var formdata = JSON.stringify(this.form);
      let response = await EventRepository.processPayment(formdata);
      let data = response.data
      this.failed = false
      if(!data.status == 200){
           this.failed =true
           this.error= data.message
           this.clicked = false;
           return
      }
      this.payment = data.data;
      this.clicked = false;
      this.startPayment = true;
    },
    callback: async function(response){
        var return_data = response
        var txref = response.tx.txRef;
        let verify = await EventRepository.verifyPayment({txref: txref});
        if(!verify.data.status == 200 ){                         
           this.failed =true
        }else{    
             if (return_data.tx.chargeResponseCode == "00" || return_data.tx.chargeResponseCode == "0") {
                this.paymentComplete = true
                this.buttonUrl = verify.data.data
                this.success = true
                window.close(); 
            } else {
                this.failed =true
                console.log('Faileddd!!!')
            } 
            window.close(); 
        } 
        window.close(); 
    },
    close: function(){
        console.log("Payment closed")
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.name = "";

      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    },
    myDefaultImage(e) {
      e.target.src = "assets/img/img-1.jpg";
    }
  },
  computed: {
    computedEvents() {
      return this.event_details;
    },
    validation() {
      return this.form.name.length > 4 && this.form.name.length < 30;
    }
  }
};
</script>
<style>
    .downloadButton{
        color:white;
        font-size: 30px;
        font-weight: 400;
    }
</style>