import  api from '../const/basePoint';

const resource = '/events';

export default{
    get(){
        return api.get(`${resource}`);
    },
    getEvent(id) {
        return api.get(`${resource}/${id}`);
    },
    getTicket(id) {
        return api.get(`${resource}/type/${id}`);
    },
    processPayment(payload) {
        return api.post(`${resource}/payment`, payload)
    },
    verifyPayment(txref) {
        return api.post(`${resource}/payment/verify`, txref)
    }
};



