<template>
  <div>
    <div class="text-component text-center">
        <p>
          <small
            ><router-link :to="`/`">Go home</router-link></small
          >
        </p>
    </div>
    <div class="container max-width-adaptive-md margin-bottom-lg">
      <div v-if="this.isLoading">
        <li
          data-theme="default"
          class="stack-cards__item bg radius-lg shadow-md js-stack-cards__item"
        >
        </li>
      </div>
      <div v-else>
        <li
          data-theme="default"
          class="stack-cards__item bg radius-lg shadow-md js-stack-cards__item"
        >
          <div class="grid">
            <div class="col-6 flex items-center height-100%">
              <div class="text-component padding-md ">
                <h2>{{ event.name }}</h2>
                <p class="display@xs">Address: {{ event.address }}</p>
                  <p class="display@xs">Date: {{ event.date }}</p>
                <div v-if="hasTicket">
                  <p class="display@xs">Select ticket type </p>
                  <div >
                    <ul>
                      <v-select label="name" :options="event.event_kinds" @input="setSelected"></v-select>
                    </ul>
                  </div><br /><br />
                  
                </div>
                <div v-else>
                    We're sorry. There are no ticket(s) available for this event yet.
                    <br /><br />
                    <p>
                        <router-link to="/"  class="btn btn--accent">Back to Events</router-link>
                    </p>
                </div>
               
              </div>
            </div>
            <div class="col-6 height-100%">
              <img
                class="block lazy width-100% height-100% object-cover"
                :src=event.image
                @error="myDefaultImage"
              />
            </div>
          </div>
        </li>
      </div>
    </div>
    <div class="container max-width-adaptive-sm">
      <div class="text-component line-height-lg v-space-md">
        <p></p>
      </div>
    </div>
  </div>
</template>
<style scoped>
.object-cover{
  background-color:#b0897e;
}
</style>
<script>
import { repositoryFactory } from "../respositories/repositoryFactory";
import router from "@/router"
import "vue-select/dist/vue-select.css";
const EventRepository = repositoryFactory.get("events");

export default {
  name: "event",
  data() {
    return {
      isLoading: false,
      event: [],
      options: []
    };
  },
  created() {
    this.getEvent();
  },
  methods: {
    getEvent: async function() {
      this.isLoading = true;
      const { data } = await EventRepository.getEvent(this.$route.params.code.toLowerCase());
      this.isLoading = false;
      console.log(this.isLoading)
      this.event = data.data;
      
    },
    myDefaultImage(e) {
      e.target.src = "assets/img/img-1.jpg";
    },
    setSelected(value) {
      console.log(value.id)
      localStorage.setItem('event_id',value.id);
       router.push('/checkout');
     }
  },
  computed: {
    computedEvents() {
      return this.event.slice(0, 10);
    },
    hasTicket: function() {
      return this.event.event_kinds.length;
    }
  }
};
</script>
